import React from 'react';
import Types from 'prop-types';
import classNames from 'classnames';
import '@transferwise/neptune-css/dist/css/drawer.css';
import Close from '@transferwise/icons/react/close';
import { Dimmer, Slider } from '..';

import KEY_CODES from '../common/keyCodes';

const Drawer = ({
  open,
  position,
  onClose,
  children,
  fadeContentOnExit,
  headerTitle,
  footerContent,
  spacing,
}) => {
  const handleOnKeyDown = event => {
    if (
      event.keyCode === KEY_CODES.ESCAPE ||
      event.key === 'Escape' ||
      event.keyCode === KEY_CODES.ENTER ||
      event.key === KEY_CODES.ENTER
    ) {
      onClose(event);
    }
  };

  return (
    <Dimmer open={open} fadeContentOnExit={fadeContentOnExit} onClose={onClose}>
      <Slider open={open} position={position}>
        <div
          className={classNames('drawer ', {
            'drawer--vertical': position === 'left' || position === 'right',
            'drawer--horizontal': position === 'top' || position === 'bottom',
          })}
        >
          <div
            className={classNames('drawer-header ', {
              'drawer-header--withborder': headerTitle,
              'p-x-5': spacing === 'lg',
            })}
          >
            <div
              role="button"
              className="drawer-header--close"
              tabIndex={0}
              onClick={onClose}
              onKeyDown={handleOnKeyDown}
            >
              <Close />
            </div>
            {headerTitle && <h3 className="drawer-header--title m-t-1 m-l-2">{headerTitle}</h3>}
          </div>
          {children && (
            <div
              className={classNames('drawer-content ', {
                'p-y-5': headerTitle,
                'p-x-5': spacing === 'lg',
              })}
            >
              {children}
            </div>
          )}
          {footerContent && (
            <div
              className={classNames('drawer-footer ', {
                'p-x-5': spacing === 'lg',
              })}
            >
              {footerContent}
            </div>
          )}
        </div>
      </Slider>
    </Dimmer>
  );
};

Drawer.propTypes = {
  open: Types.bool,
  fadeContentOnExit: Types.bool,
  position: Types.oneOf(['top', 'bottom', 'left', 'right']),
  children: Types.node,
  onClose: Types.func,
  headerTitle: Types.string,
  spacing: Types.string,
  footerContent: Types.node,
};

Drawer.defaultProps = {
  open: false,
  children: null,
  fadeContentOnExit: false,
  position: false,
  onClose: null,
  headerTitle: null,
  footerContent: null,
  spacing: 'lg',
};

export default Drawer;



