import React, { Component } from 'react';
import '@transferwise/neptune-css/dist/css/flex.css';
import classNames from 'classnames';

import Slider from './Slider';
import { generateCodeBlock, generateInput, generateState } from '../../docs/utils';

const positions = [
  { label: 'Right', value: 'right' },
  { label: 'Left', value: 'left' },
  { label: 'Top', value: 'top' },
  { label: 'Bottom', value: 'bottom' },
];

const KNOBS = {
  knobs: [
    { type: 'checkbox', label: 'Open', state: 'open', defaultState: false },
    {
      type: 'checkbox',
      label: 'Show slider border',
      state: 'showSliderBorder',
      defaultState: false,
    },
    {
      type: 'checkbox',
      label: 'Position fixed?',
      state: 'sliderPositionFixed',
      defaultState: false,
    },
    {
      type: 'select',
      label: 'Open Direction',
      state: 'position',
      options: positions,
      defaultState: { ...positions[0] },
    },
  ],
};

export default class SliderDocs extends Component {
  state = { ...generateState(KNOBS) };
  // Forces remount of component. This forces CSSTransition to re-render with new classes.
  // If show border gets applied while Slide is open in order for border class to get applied
  // CSSTransition needs to get re-rendered. This won't represent an issue in real applications as
  // border or position fixed will always be applied when slide is open and they are not going to change.
  key = 0;

  render() {
    this.key += 1;
    const { open, position, sliderPositionFixed, showSliderBorder } = this.state;
    const styleContent = {
      width: position.value === 'left' || position.value === 'right' ? 350 : '100%',
      height: position.value === 'top' || position.value === 'bottom' ? 'auto' : '100%',
    };
    return (
      <div className="container">
        <section className="section" id="slider">
          <div className="row">
            <div className="col-md-6">
              <h2>Slider</h2>
              <span role="img" aria-label="uhm">
                Simple slider. The width or the height of the slide are dictate by its content width
                and height. Additional classes can be passed to show the slider full screen or with
                a border. In order for those classes to be applied CSStransition needs to be
                triggered
              </span>
              {generateCodeBlock('Slider', KNOBS, this)}
            </div>
            <div className="col-md-6">
              <div
                className="col-xs-12 m-b-5 p-x-0"
                style={{ height: '300px', overflow: 'hidden', border: '1px solid #d3d5d8' }}
              >
                <Slider
                  open={open}
                  position={position.value}
                  fullParentWidth
                  sliderPositionFixed={sliderPositionFixed}
                  showSliderBorder={showSliderBorder}
                  key={this.key}
                >
                  <div
                    className={classNames(
                      'd-flex',
                      'justify-content-around',
                      'align-items-start',
                      'flex-wrap',
                    )}
                    style={{ padding: '20px', ...styleContent }}
                  >
                    <p>
                      Cat ipsum dolor sit amet, purr when being pet. Fight own tail weigh eight
                      pounds but take up a full-size bed fall over dead (not really but gets
                      sypathy) love you, then bite you or sit in box. Spill litter box, scratch at
                      owner, destroy all furniture, especially couch allways wanting food or chase
                      the pig around the house
                    </p>
                  </div>
                </Slider>
              </div>

              <div className="row">{KNOBS.knobs.map(knob => generateInput(knob, this))}</div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}



