import React from 'react';
import Types from 'prop-types';
import classNames from 'classnames';
import CSSTransition from 'react-transition-group/CSSTransition';
import '@transferwise/neptune-css/dist/css/slider.css';

export const EXIT_ANIMATION = 600;

const Slider = ({ open, position, children, sliderPositionFixed, showSliderBorder }) => (
  <CSSTransition
    in={open}
    // Wait for animation to finish before unmount.
    timeout={{ enter: 0, exit: EXIT_ANIMATION }}
    classNames={classNames(
      `slider--open-${position}`,
      showSliderBorder && `slider--border-${position}`,
      {
        'slider--fixed': sliderPositionFixed,
      },
      'slider',
    )}
    unmountOnExit
  >
    <div className="slider">{children}</div>
  </CSSTransition>
);

Slider.propTypes = {
  open: Types.bool,
  sliderPositionFixed: Types.bool,
  showSliderBorder: Types.bool,
  position: Types.oneOf(['top', 'bottom', 'left', 'right']),
  children: Types.node,
};

Slider.defaultProps = {
  open: false,
  sliderPositionFixed: false,
  showSliderBorder: false,
  position: 'left',
  children: null,
};

export default Slider;



