import React from 'react';
import Types from 'prop-types';

import { Slider } from '..';

export const EXIT_ANIMATION = 600;

const Sticky = ({ open, position, children }) => (
  <Slider open={open} position={position} sliderPositionFixed showSliderBorder>
    {children}
  </Slider>
);

Sticky.propTypes = {
  open: Types.bool,
  position: Types.oneOf(['top', 'bottom', 'left', 'right']),
  children: Types.node,
};

Sticky.defaultProps = {
  open: true,
  position: 'bottom',
  children: null,
};

export default Sticky;



